import React, {useState} from 'react'
import Modal from "@material-ui/core/Modal";
import { auth } from "../firebase";
import "./Modal_Comp.css"
import {ReactComponent as User} from "../images/user.svg" 
import {ReactComponent as Email} from "../images/email.svg" 
import {ReactComponent as Lock} from "../images/lock.svg" 
import {ReactComponent as VisiOff} from "../images/visibility_off.svg" 
import {ReactComponent as VisiOn} from "../images/visibility_on.svg" 


function Modal_Comp({darkmode, open, setOpen, openSignIn, setOpenSignIn, username, setUsername, setLoading}) {

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [visibilityIn, setVisibilityIn] = useState(false)

  const signUp = (e) => {
    e.preventDefault();
    auth
      .createUserWithEmailAndPassword(email, password)
      .then((authUser) => {
        return authUser.user.updateProfile({
          displayName: username
        })
      })
      .then(() => {
        window.location.reload();
      })
      .catch((error) => alert(error.message));
    setOpen(false);
    console.log(username.displayName)
  }
  
  
    const signIn = (e) => {
      e.preventDefault();
      auth
        .signInWithEmailAndPassword(email, password)
        .catch((error) => alert(error.message))
      setOpenSignIn(false);
    }
  
  return (
    <div>
      <Modal 
        open={open}
        onClose={()=> setOpen(false)}
        >
        <div
          // style={modalStyle}
          // className={classes.paper}
          className={`modal ${darkmode ? "darkmode__modal" : ""}`}
        >
          <form >
            <input type="text"
              placeholder="username"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="modal__text userUp"/>
            <input type="text"
              placeholder="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="modal__text emailUp"/>
            <input type={visibilityIn ? "text" : "password"}
              placeholder="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="modal__text passUp"/>
            <button onClick={signUp}
              className="sign__button"
            >Sign Up</button>
            <div className="userimage modal__images">
              <User className="modImage"/></div>
            <div className="emailimage modal__images">
              <Email className="modImage"/></div>
            <div className="lockimage modal__images">
              <Lock className="modImage"/></div>
            <div className="userimage__tail modal__images__tail"></div>
            <div className="emailimage__tail modal__images__tail"></div>
            <div className="lockimage__tail modal__images__tail"
              onMouseUp={() => setVisibilityIn(!visibilityIn)}
            >
            {
                visibilityIn ? <VisiOn  className="modImage"/> : 
              <VisiOff  className="modImage"/>
              }
            </div>

          </form>
        </div>
      </Modal>

      <Modal 
          open={openSignIn}
          onClose={()=> setOpenSignIn(false)}
          >
        <div
          // style={modalStyle} className={classes.paper}
          className={`modal ${darkmode ? "darkmode__modal" : ""}`}>
          <form>
            <input type="text"
              placeholder="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="modal__text emailIn"
            />
          
            <input type={visibilityIn ? "text" : "password"}
              placeholder="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="modal__text passIn"
            />
            <button onClick={signIn}
              className="sign__button"
            
            >Sign In</button>
            <div className="emailimageIN modal__images">
              <Email /></div>
            <div className="lockimageIN modal__images">
              <Lock /></div>
            <div className="emailimage__tailIN modal__images__tail"></div>
            <div className="lockimage__tailIN modal__images__tail"
              onMouseUp={() => setVisibilityIn(!visibilityIn)}
            >
              {
                visibilityIn ? <VisiOn  className="modImage"/> : 
              <VisiOff  className="modImage"/>
              }
            </div>

          </form>
        </div>
      </Modal>
      
    </div>
  )
}

export default Modal_Comp
