import React, { useState } from 'react'
import "./EyeOfHorus.css"
import {ReactComponent as Exclamation} from "../images/exclamation.svg"
import {ReactComponent as Create} from "../images/create.svg"
import {ReactComponent as Question} from "../images/question.svg"
import Form from "./Form"
import Features from "./Features"

function EyeOfHorus({darkmode, setDarkmode, username , eyeopen, setEyeOpen}) {
  const [formopen, setFormOpen] = useState(false);
  const [featuresopen, setFeaturesOpen] = useState(false);
  // NOTICE: IMAGE ERROR 12/7/20

  function eyeOpened(){
    setEyeOpen(!eyeopen)
    if (eyeopen === true) {
      setFormOpen(false)
      setFeaturesOpen(false)
    }

  }

  const formOpened = (e) => {
    e.preventDefault();
    setFormOpen(!formopen)
    setFeaturesOpen(false)
  
  }
  const featuresOpened = (e) => {
    e.preventDefault();
    setFeaturesOpen(!featuresopen)
    setFormOpen(false)

  }
    
  function backgroundCleared(){
    setFormOpen(false)
    setFeaturesOpen(false)
  }
  return (

    <div className={`form__container`}
    >

      <div className={`backgroundclear 
      ${formopen || featuresopen ? "" : "backgroundunclear"}
        `}
        onClick={backgroundCleared}></div>
      <div className={`design__form ${eyeopen ? "form_hover" : ""}`}
      onClick={eyeOpened}>
      
      <div className={`design1__square ${eyeopen? "form_designHover" : ""}`}>
        <div className={`design2__square ${eyeopen ? "form_designHover2" : ""}`}>

            <div className={`period ${eyeopen? "period__remove" : ""}`}></div>
          <Exclamation className={`question ${eyeopen ? "form_designQuestion" : ""}`}/>
          {/* <img className="question" src={Question} alt=""></img> */}
        </div>
        </div>
      </div>
      <div className="form__buttons">
        <div className={`create__post ${eyeopen ? "form__buttons_hover" : ""}`}
          onMouseUp={formOpened}>
          <Create className="button__create"/>
        </div>
        <div className={`features__guide ${eyeopen ? "form__buttons_hover" : ""}`}
        onMouseUp={featuresOpened}>
        <Question className="button__question"/>

        </div>
        <div className={`unknown2 ${eyeopen ? "form__buttons_hover" : ""}`}>
          <div className="dark__mode"
          onMouseUp={()=> setDarkmode(!darkmode)}
          ></div>


        </div>
      </div>
      {/* // create ? ( */}
      <Features featuresopen={featuresopen} setFeaturesOpen={setFeaturesOpen}/>
      <Form formopen={formopen} darkmode={darkmode} setFormOpen={setFormOpen} username={username}/>
    </div>
    
  )
}

export default EyeOfHorus
