import React, {useState, useEffect} from 'react'
import { Avatar } from "@material-ui/core" 
import "./Comment.css"

function Comment({ id, darkmode, username, text }) {
    
    const [heightReached, setHeightReached] = useState(false)
    const [fullcomment, setFullComment] = useState(false);

    useEffect(() => {
        const commentElementHeight = document.getElementById(id).getBoundingClientRect().height
        if (commentElementHeight > 130)
            setHeightReached(true)
        else
            setHeightReached(false)
            
    }, [heightReached, id])
    
    return (
        <div className="comment__App">
            <div className={`individual__comment ${darkmode ? "darkmode__indivcomment" : ""}`}
            style={{maxHeight: fullcomment ? "" : "190px"}}
            >

                { heightReached ? 
                    <div className={`more__box ${fullcomment ? "nograd" : ""} ${darkmode ? "dm__more__box" : ""}`}
                    onMouseUp={(() => setFullComment(!fullcomment))}>
                        <div className={`triangulation ${fullcomment ? "clicked__tri" : ""}`}>
                            <div className={`leftdot tridot ${fullcomment ? "clicked__dot" : ""} ${darkmode ? "dm__dots" : ""}`}></div>
                            <div className={`topdot tridot ${fullcomment ? "clicked__dot2" : ""} ${darkmode ? "dm__dots" : ""}`}></div>
                            <div className={`rightdot tridot ${fullcomment ? "clicked__dot" : ""} ${darkmode ? "dm__dots" : ""}`}></div>
                        </div>
                    </div> : ""    
                }
                    <Avatar className="core__avatar"
                        style={{ width: "10px" }}/>
                    <div className="core__user_et_comment"
                    >
                        <h1>{username}</h1>
                    <h1 className="core__comment"
                        id={`${id}`}
                    >{text}</h1>
                    </div>
                </div>
        </div>
    )
}

export default Comment
