import React, {useState, useEffect} from "react";
import "./App.css";
import { auth, db } from "./firebase";
import ModalComp from "./Components/Modal_Comp"
import Posts from "./Components/Posts";
import EyeOfHorus from "./Components/EyeOfHorus";

function App() {
  const [open, setOpen] = useState(false);
  const [posts, setPosts] = useState([]);
  const [openSignIn, setOpenSignIn] = useState(false);
  const [username, setUsername] = useState('');
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true)
  const [techanim, setTechAnim] = useState(false)
  const [eyeopen, setEyeOpen] = useState(false);
  const [darkmode, setDarkmode] = useState(() =>{
    const localData = localStorage.getItem('darkening');
    return localData ? JSON.parse(localData) : false;
  });
  var lister = []
  var i=0
  for (i; i < 121; i++){
    lister.push("")
  }

  useEffect(() => {
    localStorage.setItem('darkening', JSON.stringify(darkmode))
  }, [darkmode])  

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((authUser) => {
      if (authUser)
        setUser(authUser);
      else 
        setUser(null);
    })
    return () => {
      unsubscribe();
    }
  }, [user, username]);
  
  useEffect(() => {
    db.collection('posts').orderBy('timestamp', 'desc')
      .onSnapshot(snapshot => {
      setPosts(snapshot.docs.map((doc) => ({
        id: doc.id,
        data: doc.data()
      })));
    })
  }, []);

  return (
    <div className="App">
      <div className="App__background"></div>
      <div className={`entirety ${darkmode ? "darkmode__entirety" : ""}`}>
        <div id="bgtop" className={`techno__background ${techanim ? "techanimtop" : ""}`}>
        
        {
            lister.map((i) => (
              <div key={i.id} className={`box ${darkmode ? "darkmode__box" : ""}`}></div>
              ))
            }
      <div className={`fader ${darkmode? "darkmode__fader" : ""}`}></div>
      </div>
        <div id="bgbottom" className={`techno__background ${techanim ? "techanimbottom" : ""}`}>
          {
            lister.map((o) => (
              <div key={o.id} className={`box ${darkmode ? "darkmode__box" : ""}`}></div>
              ))
          }
          <div className={`fader ${darkmode? "darkmode__fader" : ""}`}></div>
        </div>
      </div>    

      <ModalComp open={open} setOpen={setOpen}
        openSignIn={openSignIn} setOpenSignIn={setOpenSignIn}
        username={username} setUsername={setUsername}
        user={user} setUser={setUser}
        setLoading={setLoading}
        darkmode={darkmode}
      />
      
      {
        loading ? "" :
        (user ? (
          
          <div className={`outer ${techanim ? "appvision" : ""}`}>
            <button className= {`logout ${darkmode ? "darkmode__mainbutton" : ""}`} onClick={() => auth.signOut()}>LOGOUT</button>
              <EyeOfHorus posts={posts} setPosts={setPosts} username={user.displayName} user={user.displayName}
                eyeopen={eyeopen} setEyeOpen={setEyeOpen}
                darkmode={darkmode} setDarkmode={setDarkmode}
              />
          </div>
        ) : (
          <div className={`signer  ${techanim ? "appvision" : ""}`}>
            <button className={`inner__signer ${darkmode ? "darkmode__mainbutton" : ""}`} onClick={() => setOpenSignIn(true)}>SIGN IN</button>
            <button className={`inner__signer ${darkmode ? "darkmode__mainbutton" : ""}`} onClick={() => setOpen(true)}>SIGN UP</button>
          </div>))
      }
      
      
      <Posts posts={posts} user={user}
        loading={loading} setLoading={setLoading}
        techanim={techanim} setTechAnim={setTechAnim}
        eyeopen={eyeopen} setEyeOpen={setEyeOpen}
        darkmode={darkmode} setDarkmode={setDarkmode}  
      />

      </div>
  );
}

export default App;
