import React, {useState} from 'react'
import './Form.css'
import { storage, db } from "../firebase"
import firebase from "firebase"
import insertphoto from "../images/insert_photo.svg"

function Form({darkmode, username, formopen, setFormOpen}) {

  const [image, setImage] = useState(null);
  const [caption, setCaption] = useState('');
  const [content, setContent] = useState('');
  const [imagepreview, setImagePreview] = useState(null);
  const [progress, setProgress] = useState(0);
  const [posting, setPosting] = useState(false);
  const [fullview, setFullview] = useState(false);

  const imageSelector = (e) => {
    if (e.target.files.length > 0) {
      setImage(e.target.files[0]);
      setImagePreview(URL.createObjectURL(e.target.files[0]))
    }
  };
  

  const addPost = (e) => {
    e.preventDefault();
    const fileInput = document.getElementById("file")
    if (caption !== '') {
        if (image === null) {
          e.preventDefault();
          db.collection("posts").add({
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            caption: caption,
            imageUrl: image,
            username: username,
            content: content
          })
            .catch(function (error) {
              console.error("Error writing document: ", error);
            });
          setContent("")
          setCaption("")
          setFormOpen(false)
        }
      
        else {
          const postTask = storage.ref(`images/${image.name}`).put(image);
          postTask.on(
            "state_changed",
            (snapshot) => {
              setPosting(true)
              const progress = Math.round(
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100)
              setProgress(progress);
            },
            (error) => {
              alert(error.message);
            },
            () => {
              storage
                .ref("images")
                .child(image.name)
                .getDownloadURL()
                .then(url => {
                  db.collection("posts").add({
                    timestamp: firebase.firestore.FieldValue.serverTimestamp(),
                    caption: caption,
                    imageUrl: url,
                    username: username,
                    content: content
                  })
                
                  setContent("")
                  setCaption("")
                  fileInput.value = null;
                  setImage(null)
                  setFormOpen(false)
                  setPosting(false)
                })
            })
        }
      }
    else {
      alert("Caption is required!")
      }
  }

  return (
    <div className="form__container">
      <div className={`form__border ${formopen ? "showing" : ""} ${darkmode ? "darkmode__formborder" : ""}`}>
        <div className={`form__top__dot`}>
          <div className={`dot ${darkmode ? "darkmode" : ""}`}></div>
          <div className={`dot ${darkmode ? "darkmode" : ""}`}></div>
        </div>
        <div className="form__bottom__dot">
          <div className={`dot ${darkmode ? "darkmode" : ""}`}></div>
          <div className={`dot ${darkmode ? "darkmode" : ""}`}></div>
        </div>
        
        {
          caption.length > 176 ? 
            (<div className="error" title="Maximum Character Reached">
            <div className={`error__line1 error_line ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            <div className={`error__line2 error_line ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            <div className={`error__line3 error_line ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            <div className={`error__dot1 error_dot ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            <div className={`error__dot2 error_dot ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            <div className={`error__dot3 error_dot ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            <div className={`error__exUp error_ex ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            <div className={`error__exDown error_ex ${darkmode? "darkmode_shadow" : ""}`} style={{animationName: darkmode ? "darkmodeerror" : "erroranim"}}></div>
            </div>) : ""
        }


        <div className={`App__header ${formopen ? "showing" : ""} ${darkmode? "darkmode" : ""}`}
          id={`${formopen ? "showing" : ""}`}
        >
          {
            caption.length >= 37 || fullview ?
              (<div className="caption__fullview">
                <textarea className={`cf__textarea ${darkmode ? "darkmode__cft" : ""}`}
                  onChange={(e) => setCaption(e.target.value)}
                  value={caption}
                  maxLength="177"
                  onFocus={() => setFullview(true)}
                  onBlur={() => setFullview(false)}
                  
                />
              </div>
              ) : ""
          }
          <input type="text"
            className={`text__caption textfield ${darkmode ? "darkmode__textfield" : ""}`}
            placeholder="Insert Title Here"
            maxLength="177" 
            value={caption}
            onChange={(e) => setCaption(e.target.value)}
            
          />
          <textarea className={`text__content textfield ${darkmode ? "darkmode__textfield" : ""}`}
            placeholder="What is on thy mind?"
            value={content}
            onChange={(e) => setContent(e.target.value)} /> 
          <input type="file"
            name="file"
            id="file"
            className="inputfile"
            onChange={imageSelector} />
          <label htmlFor="file" className={`labelfile ${darkmode ? "darkmode__lf" : ""}`}>
            <img src={insertphoto} className="file__Image" alt=""></img> 
          </label>
          <button className={`post__button ${posting ? "unclickable" : ""}`}
              onMouseOver={()=> setContent(content => content.trim())}
          
          >
            <div className="mask1 mask"></div>
            <div className="mask2 mask"></div>
            <div className="mask3 mask"></div>
            <div className="mask4 mask"></div>
            <label className="post__label"
            >POST</label>
            <div className={`post__background ${darkmode ? "darkmode__pm" : ""}`}
              onMouseUp={addPost}
              onMouseOver={()=> setCaption(caption => caption.trim())}
              >
              <div className={`jail ${posting ? "jailed" : ""}`}>
                <span className="line"></span>
                <span className="line"></span>
                <span className="line"></span>
                <span className="line"></span>
              </div>
            </div>
          </button>

          {
            posting ?
              (<progress className="form__progress" value={progress} max="100" />) : ("")
          }
        </div>
      </div>
      
      {
        image ? 
          (<div className={`image__preview ${formopen ? "showing" : ""}`}>
            <img src={imagepreview} alt="" className="imagepreview" />
          </div>) : ""
      }
    </div>

  )
}

export default Form
