import React, {useState} from 'react'
import "./Post.css"
import PostCore from "./Post_Core"
import {ReactComponent as ThumbUp} from "../images/thumbUp.svg"
import useFitText from "use-fit-text";
import {Avatar} from "@material-ui/core" 

function Post({darkmode, user, setEyeOpen, techanim, setTechAnim, postId, username, caption, imageUrl, content}) {

    const { fontSize, ref } = useFitText();
    const [liked, setLiked] = useState(false);
    const [likecounter, setLikecounter] = useState(10);
    const [hidden, setHidden] = useState(true);
    const [postshidden, setPostshidden] = useState(false);


    const unhide = (e) => {
        e.preventDefault();
        setHidden(false);
        setPostshidden(true);
        setTechAnim(true);
        setEyeOpen(false);
    }
    const likedFunction = (e) => {
        e.preventDefault();
        setLiked(!liked)

        if(liked === false)
        setLikecounter(counter => counter + 1)
        else
        setLikecounter(counter => counter + -1)
    }
    function animend(){
        setPostshidden(false)
    }
    return (
        <div className={`post ${postshidden ? "postAnim": ""}  ${techanim ? "postvision" : ""}`}
            onAnimationEnd={animend}>
            {/* Profile Name & Image */}
            <div className={`profile__image ${techanim ? "zerovision" : ""}`}>

                <div className="user__persona">
                    <Avatar />
                    {/* <img src={imageUrl} alt="pekkkoooo" srcSet=""/> */}
                </div>
                <h1 className="user__name">{username}</h1>
            </div>
                    
            {/* Content Beside Profile Name & Image */}
            <div className={`content ${techanim ? "zerovision" : ""} ${darkmode? "darkmode__border" : ""}`} onDoubleClick={unhide}>
                <div className={`left__dot ${darkmode ? "darkmode__dot" : ""}`}></div>
                <div className={`right__dot ${darkmode ? "darkmode__dot" : ""}`}></div>
                <div className="text__et__like" ref={ref} style={{fontSize}}>
                    <div className="caption" style={{display: imageUrl!== null ? "grid" : "" }}>
                        {
                            (imageUrl !== null) ?
                                <div className="caption__image">
                                    <img className={`image__container ${darkmode ? "darkmode__imagecon" : ""}`}
                                        src={imageUrl} alt=""
                                    />
                                    
                            </div> : ""
                        }
                        <h1 className="caption__text">{caption.substring(0, 100)}
                            {
                                caption.length > 100 ? (" . . .")
                                    : ("")
                            }
                        </h1>
                    </div>
                    <div className="like__container">
                        <ThumbUp className={`img__button1 like__et__counter 
                            ${liked && darkmode ? "darkmode__like__active1" : ""}
                            ${liked ? "like__active1" : ""}
                            ${darkmode ? "darkmode__button" : ""}`}
                            onClick={likedFunction}/>
                        <h1 className="like__counter like__et__counter">{likecounter}</h1>
                    </div>
                </div>
            </div>

            <PostCore liked={liked} setLiked={setLiked} hidden={hidden} setHidden={setHidden}
                postId={postId} username={username} imageUrl={imageUrl} content={content}
                caption={caption} likecounter={likecounter} setLikecounter={setLikecounter}
                postshidden={postshidden} setPostshidden={setPostshidden}
                setTechAnim={setTechAnim} user={user} darkmode={darkmode} />
            </div>
    )
}
export default Post