import React, { useState, useEffect } from 'react'
import "./Post_Core.css"
import { db } from "../firebase"
import {ReactComponent as ThumbUp} from "../images/thumbUp.svg"
import {ReactComponent as Save} from "../images/save.svg"
import {ReactComponent as Back} from "../images/back.svg"
import useFitText from "use-fit-text";
import firebase from "firebase"
import Comment from "./Comment"

function Post_Core({darkmode, user, setTechAnim, liked, setLiked, hidden, setHidden, postId, username, imageUrl, content, caption, likecounter, setLikecounter,
    setPostshidden }) {

    const { fontSize, ref } = useFitText();
    const [comments, setComments] = useState([])
    const [comment, setComment] = useState('')
    const [big, setBig] = useState(false);
    const [isHovered, setIsHovered] = useState(false)
  
    const [commenthide, setCommenthide] = useState(true);
    var lister = []
    var i=0
    for (i; i < 65; i++){
      lister.push("")
    }

    useEffect(() => {
        let unsubscribe;
        if (postId) {
            unsubscribe = db
                .collection("posts")
                .doc(postId)
                .collection("comments")
                .orderBy('timestamp', 'desc')
                .onSnapshot((snapshot) => {
                    setComments(snapshot.docs.map((doc) =>
                    ({
                        id: doc.id,
                        data: doc.data()
                    })));
                });
        }
        return () => {
            unsubscribe();
        };
    }, [postId]);

    const postComment = (e) => {
        e.preventDefault();
        db.collection("posts")
            .doc(postId)
            .collection("comments")
            .add({
                timestamp: firebase.firestore.FieldValue.serverTimestamp(),
                text: comment,
                username: user.displayName
            });
        setComment('');

    }
    const hide = (e) => {
        setHidden(true);
        setPostshidden(false);
        setTechAnim(false);
    
    }
    const likedFunction = (e) => {
        e.preventDefault();
        setLiked(!liked)
        if(liked === false)
        setLikecounter(counter => counter + 1)
        else
        setLikecounter(counter => counter + -1)
            
    }

    const bigImage = (e) => {
        e.preventDefault()
        setBig(!big)
    }

    return (
        <div>
            <div className={`content__core ${hidden ? "contentIndex" : "unhidden contentIndex"}`}>
                <div className={`fullview ${big ? "fullview__showing" : ""}`}
                    onMouseUp={() => setBig(false)}>

                    <div className="fullview__image__container">
                        <img src={imageUrl} alt="" className="fullview__image"/>
                    </div>
                </div>
                
                <div className={`content__core__body ${darkmode ? "darkmode__border" : ""}`}>
                   
                    <div className="ccb__linear__background"></div>
                    <div className="ccb__elements">
                    <div className="header"  ref={ref} style={{fontSize}}>
                        <div className="header__title" >
                            <h1 className="core__caption core__text">{caption}</h1>
                        </div>
                        <div className="header__buttons">
                            <Save className={`img__button ${darkmode? "darkmode__button" : ""}`}/>
                            <ThumbUp className={`img__button ${darkmode? "darkmode__button" : ""}
                                                ${liked ? "like__active" : ""}
                                                ${liked && darkmode ? "darkmode__like__active" : ""}`}
                                onClick={likedFunction} />
                            <h1>{likecounter}</h1>

                        </div>
                        
                    </div>
                    
                    {
                        imageUrl ? 
                            (<div className={`core__image__container ${darkmode ? "darkmode__cic" : ""}`}
                            >
                                <img src={imageUrl} alt="" className="core__image"
                                    onMouseUp={bigImage}
                                />
                            </div>)
                        :""
                        }




                    <h1 className="core__content core__text">{content}</h1>
                        <div className="comment__section">
                            <div className="post__comments">
                                {comments.map((comment) => (
                                    <Comment id={comment.id} darkmode={darkmode}
                                        username={comment.data.username} text={comment.data.text}
                                        />
                                
                                ))}
                            </div>

                            {commenthide ?
                                (<form className={`comment__box ${darkmode ? "darkmode__commentbox" : ""}`}
                                    style={{ opacity: isHovered ? 1 : 0 }}
                                    onMouseOver={() => setIsHovered(true)}
                                    onMouseOut={() => setIsHovered(false)}>
                                    <textarea
                                        maxLength="777"
                                        className={`comment__input ${darkmode ? "darkmode__commentinput" : ""}`}
                                        type="text"
                                        disabled={!isHovered}
                                        placeholder="Add a comment"
                                        value={comment}
                                        onChange={(e) => setComment(e.target.value)}
                                    />

                                    <button
                                        className={`comment__button ${darkmode ? "darkmode__commentbtn" : ""}`}
                                        disabled={!comment}
                                        type="text"
                                        onClick={postComment}
                                        onMouseOver={() => setComment(comment => comment.trim())}>
                                        comment</button>
                                </form>
                                ) : ""}
                            
                       </div>
                    </div>
                </div>
                <Back className="exit__button" onMouseUp={hide} />
                
            
            </div>

            </div>
            
    )
}

export default Post_Core
